  font-size: 20px;
}

.list-bullet {
  vertical-align: middle;
}

a:hover {
  text-decoration: none;
}

#logo-personal {
  display: flex;
  justify-content: center;
}

#logo-personal p {
  font-style: italic;
  font-weight: 300;
  text-align: center;
  width: 80%;
}

.tab-link:focus-visible {
