.project-container {
  margin-bottom: 140px;
  max-width: 1200px;
}

.project-container:nth-child(even) {
  margin-left: auto;
  margin-right: 0;
}

.main-container .project-container:last-child {
  margin-bottom: 0;
}

.project-info {
  width: 100%;
  display: flex;
  flex-direction: column-reverse;
}

.project-info a {
  margin: 32px 0;
}

/* IMAGE */

.image {
  width: 100%;
  height: 0;
  padding-top: 63%;
  background-position: center;
  background-size: cover;
  border-radius: 16px;
}

.image-container {
  width: 100%;
}

/* TAGS */

.tech-tags {
  display: flex;
  flex-wrap: wrap;
  grid-gap: 8px;
  gap: 8px;
}

.tech-tag {
  color: var(--black);
  padding: 24px 40px;
  border-radius: 50px;
  border: 1px solid var(--black);
  font-weight: 500;
  font-size: 1.25rem;
}

/* RESPONSIVENESS */


