    margin: 24px 0;
  }
}

@media screen and (max-width: 750px) {
  .project-container {
    margin-bottom: 88px;
  }

  .project-info a {
    margin: 20px 0;
  }

  .project-info svg {
    width: 44px;
  }

  .tech-tag {
    padding: 16px 32px;
    border-radius: 50px;
    font-size: 1rem;
  }
}

@media screen and (max-width: 550px) {
  .project-info a {
    margin: 16px 0;
  }
}

.link-button {
  text-decoration: none;
  color: var(--black);
  font-size: 2rem;
  font-weight: 500;
  font-family: 'Dela Gothic One', sans-serif;
  display: flex;
  align-items: center;
  white-space: nowrap;
  height: 30px;
  align-items: center;
  display: flex;
  /* transition: transform .7s ease-in-out; */
}

/* Hide extra text */
.mask {
  position: relative;
  padding: 0;
  height: 24px;
  /*  Remove overflow to see how it works　:) */
  overflow: hidden;
}

.link-container {
  transition: transform 0.4s ease;
}


.title {
  display: block;
  /*  Set same font-size and line height  */
  font-size: 24px;
  line-height: 24px;
  text-transform: uppercase;
  transition: transform 0.4s ease;
}

.link-title1 {
  transform-origin: right center;
}

.link-title2 {
  transform-origin: left center;
  transform: rotate(20deg);
}

/* Hover Action*/

/* Move up two texts (20px = font-size) */
.link-button:hover .link-container {
  transform: translateY(-24px);
}

/* Rotate texts a little bit */
.link-button:hover .link-title1 {
  transform: rotate(20deg);
}

.link-button:hover .link-title2 {
  transform: rotate(0);
}



.arrow {
  margin-left: 8px;
}

.link-button:hover {
  cursor: pointer;
  color: var(--black);
  /* transform: rotateX(90deg); */
  /* transform-style: preserve-3d; */
  /* transform-origin: bottom; */
}

.link-button:active {
  box-shadow: none;
}

.link-button:focus-visible {
  outline: 4px solid var(--orange);
}




@media screen and (max-width: 550px) {
  .mask {
    height: 16px;
  }
  .title {
    font-size: 16px;
    line-height: 16px;
  }
  .link-button:hover .link-container {
    transform: translateY(-16px);
  }
}

@media screen and (max-width: 400px) {
  .mask {
    height: 16px;
  }
  .title {
    font-size: 16px;
    line-height: 16px;
  }
  .link-button:hover .link-container {
    transform: translateY(-16px);
  }
}


#wrapper-animation {
  position: relative;
  margin-top: -100px;
  z-index: 1000;
}

.animation-image {
  width: 100%;
}

.animation-mask {
  height: 700px;
  width: 800px;
  overflow: hidden;
  display: flex;
  grid-gap: 16px;
  gap: 16px;
  border-radius: 16px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  --animation-offset: 7700px;
  --animation-duration: 45s;
  margin-bottom: 32px;
}

@keyframes changeYup {
  100% {
    transform: translateY(calc(var(--animation-offset)*-1));
  }
}

@keyframes changeYdown {
  100% {
    transform: translateY(var(--animation-offset));
  }
}

.animation-column {
  display: flex;
  flex-direction: column;
  grid-gap: 16px;
  gap: 16px;
  animation-name: changeYup;
  animation-duration: var(--animation-duration);
  animation-iteration-count: infinite;
  animation-direction: alternate;
  animation-timing-function: linear;
}

.animation-column:nth-child(even) {
  margin-top: calc(var(--animation-offset)*-1);
  animation-name: changeYdown;
  animation-duration: var(--animation-duration);
  animation-iteration-count: infinite;
  animation-direction: alternate;
  animation-timing-function: linear;
}

@media screen and (max-width: 1350px) {
  .animation-mask {
    --animation-offset: 5500px;
  }
}
@media screen and (max-width: 1000px) {
  .animation-mask {
    --animation-offset: 4000px;
  }
}
@media screen and (max-width: 750px) {
  .animation-mask {
    height: 500px;
    --animation-offset: 3000px;
  }
}
@media screen and (max-width: 550px) {
  .animation-mask {
    height: 400px;
    --animation-offset: 2000px;
  }
}
@media screen and (max-width: 400px) {
  .animation-column {
      grid-gap: 8px;
      gap: 8px;
  }
  .animation-mask {
    --animation-offset: 1000px;
    height: 300px;
    grid-gap: 8px;
    gap: 8px;
  }
}
@media screen and (max-width: 250px) {
  .animation-mask {
    --animation-offset: 500px;
  }
}
@media screen and (max-width: 150px) {
  .animation-mask {
    --animation-offset: 300px;
  }
}

